"use client";
import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { cn } from "@/lib/utils";

export const BackgroundBeams = ({ className }: { className?: string }) => {
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
    }, []);

    const beams = [
        {
            initialX: 10,
            translateX: 10,
            duration: 7,
            repeatDelay: 3,
            delay: 2,
            left: "10%",
        },
        {
            initialX: 600,
            translateX: 600,
            duration: 3,
            repeatDelay: 3,
            delay: 4,
            left: "25%",
        },
        {
            initialX: 100,
            translateX: 100,
            duration: 7,
            repeatDelay: 7,
            className: "h-6",
            left: "40%",
        },
        {
            initialX: 400,
            translateX: 400,
            duration: 5,
            repeatDelay: 14,
            delay: 4,
            left: "55%",
        },
        {
            initialX: 800,
            translateX: 800,
            duration: 11,
            repeatDelay: 2,
            className: "h-20",
            left: "70%",
        },
        {
            initialX: 1000,
            translateX: 1000,
            duration: 4,
            repeatDelay: 2,
            className: "h-12",
            left: "85%",
        },
        {
            initialX: 1200,
            translateX: 1200,
            duration: 6,
            repeatDelay: 4,
            delay: 2,
            className: "h-6",
            left: "95%",
        },
    ];

    if (!mounted) {
        return null;
    }

    return (
        <div
            className={cn(
                "absolute inset-0 h-full w-full overflow-hidden pointer-events-none",
                className
            )}
        >
            {beams.map((beam, idx) => (
                <motion.div
                    key={`beam-${idx}`}
                    initial={{
                        translateY: beam.initialX,
                        translateX: 0,
                    }}
                    animate={{
                        translateY: beam.translateX,
                        translateX: 0,
                    }}
                    transition={{
                        duration: beam.duration || 8,
                        repeat: Infinity,
                        repeatType: "loop",
                        ease: "linear",
                        delay: beam.delay || 0,
                        repeatDelay: beam.repeatDelay || 0,
                    }}
                    style={{
                        position: "absolute",
                        top: 0,
                        left: beam.left,
                    }}
                    className={cn(
                        "absolute h-2 w-px rounded-full bg-gradient-to-t from-gold/0 via-gold/40 to-gold/0",
                        beam.className
                    )}
                />
            ))}
            <div className="absolute inset-0 bg-gradient-to-t from-white via-white/80 to-transparent pointer-events-none" />
        </div>
    );
};
